<template>
  <div class="container-wrapper">
      <div class="tio-section tio-section--featured-posts">
           <div class="tio-section__header">
                <span class="tio-section__title">Nhng bi vit ni bt Tun ny</span>
                  <div class="tio-section__link">
                    <span>Xem tt c bi vit</span>
                    <i class="fas fa-chevron-right"></i>
                </div>
            </div>
            <div class="tio-section__body">
                <div class="section-featured-post">
                    <el-row :gutter="32">
                        <el-col :md="14" :lg="14">
                            <div v-for="item in slicedfirstArray"
                                :key="item.id"
                                class="section-featured-post__full-thumbnail" 
                                :style="{ backgroundImage: 'url(' + item.imagePath + ')' }">
                                <span class="title">{{ item.content }}</span>
                            </div>
                        </el-col>
                        <el-col :md="10" :lg="10"> 
                             <ul class="lastest-news-lists lastest-news-lists--small">
                                <PostItem v-for="item in listLastestNews"
                                    :key="item.id"
                                    :link="item.link"
                                    :image_path="item.imagePath"
                                    :content="item.content"/>
                            </ul>
                        </el-col>
                    </el-row>
                </div>
            </div>
      </div>
  </div>
</template>

