import PostItem from '../../../components/PostItem.vue'
export default {
    name: "FeaturedPost",
    components: {
         PostItem
    },
    data () {
        return {
              listLastestNews: [
                {
                    id: 1,
                    link: 'https://ictnews.vietnamnet.vn/san-pham-so/apple-ra-mat-iphone-12-mini-gia-699-usd-iphone-12-gia-799-usd-iphone-12-pro-gia-999-usd-iphone-12-pro-max-gia-1-099-usd-266783.html#:~:text=iPhone%2012%20nh%E1%BA%ADn%20%C4%91%E1%BA%B7t%20tr%C6%B0%E1%BB%9Bc,s%E1%BA%BD%20%C4%91%C6%B0%E1%BB%A3c%20Apple%20gi%E1%BA%A3m%20gi%C3%A1.',
                    imagePath: 'https://f17-zpg.zdn.vn/3483596001959583306/6ca9f08fb161453f1c70.jpg',
                    content: 'Apple  chnh thc ra mt iPhone 12 mini gi 699 USD, iPhone 12 gi 799 USD, iPhone 12 Pro gi 999 USD, iPhone 12 Pro Max gi 1.099 USD. C 4 mu iPhone u c trang b kt ni 5G v thit k mi l.'
                },
                {
                    id: 2,
                    link: 'https://lifestyle.vn/top-10-mau-tv-4k-tot-nhat-dau-nam-2020/',
                    imagePath: 'https://f20-zpg.zdn.vn/4002098219721900837/087eee685c7fa821f16e.jpg',
                    content: 'y u l nhng mu TV n t cc thng hiu c tn tui nh Sony, LG, Samsung cng nhng ci tn ng ch  khc trong ngnh cng nghip.'
                },
                {
                    id: 3,
                    link: 'https://lifestyle.vn/samsung-ra-mat-toan-cau-dong-tu-lanh-thoi-trang-bespoke-thiet-ke-module-doi-mau-tuy-thich/',
                    imagePath: 'https://f41-zpg.zdn.vn/1954682754057790048/efaf72a4c9b33ded64a2.jpg',
                    content: 'Samsung ra mt ton cu dng t lnh thi trang Bespoke: Thit k module, i mu ty thch'
                },
                {
                    id: 4,
                    link: 'https://lifestyle.vn/asus-ra-mat-zephyrus-duo-15-core-i9-rtx-2080-super-gpu-2-man-hinh-cam-ung-4k/',
                    imagePath: 'https://f40-zpg.zdn.vn/7758212086271823657/019dbc469650620e3b41.jpg',
                    content: 'Asus ra mt Zephyrus Duo 15: Core i9, RTX 2080 Super GPU, 2 mn hnh, cm ng 4K'
                },
                {
                    id: 5,
                    link: 'https://lifestyle.vn/15-ung-dung-goi-video-thu-vi-tren-android-khi-khong-co-facetime/',
                    imagePath: 'https://f42-zpg.zdn.vn/1691682139534391088/a27c4e06f0ec04b25dfd.jpg',
                    content: '15 NG DNG GI VIDEO TH V TRN ANDROID KHI KHNG C FACETIME'
                }
            ]
        }
    },
