        slicedfirstArray() {
            return this.listLastestNews.slice(0, 1)
        }
    }
}
</script>

<style lang="scss" scoped>
    .tio-section {
        &--featured-posts {
            background-color: transparent;
        }
        &__header {
            padding: 0 0 20px 0;
            border-bottom: 0;
        }
    }
    .section-featured-post {
        padding: 16px;
        background-color: #fff;

        &__full-thumbnail {
            height: 355px;
            padding: 24px 20px;
            display: flex;
            align-items: flex-end;
            .title {
                color: #fff;
                font-size: 22px;
                line-height: 32px;
                font-weight: 500;
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: 2;
            }
        }
    }
    .lastest-news-lists {
        &--small {
            .lastest-news-image { display: none;}
        }
        &__item {
            padding: 12px 0;
            margin: 0;
            &:first-child {
                padding-top: 0;
                border-top: none;
            }

            &:last-child {
                padding-bottom: 0;
            }
        }
    }
   
   
</style>


